<!-- 资源主页面 -->
<template>
  <div class="resource">
    <div class="main-content">
      <res-search @handleSearch="handleSearch" v-model="resourceName"></res-search>
      <div class="search-type">
        <div class="left">
          <span @click="resourceFamily=0" :class="{active:resourceFamily===0}">全部</span>
          <span @click="resourceFamily=9051" :class="{active:resourceFamily===9051}">同步课堂</span>
        </div>
        <div class="right">
          <div class="type">
            <span @click.stop="selectType=1">
              {{typeValue}}
              <i class="el-icon-arrow-down"></i>
            </span>
            <ul v-clickOutSide="handleHideSelect" v-if="selectType===1">
              <li @click="handleSelectCategory('resourceFileType',0,'不限类型')">不限类型</li>
              <li @click="handleSelectCategory('resourceFileType',9030,'视频')">视频</li>
              <li @click="handleSelectCategory('resourceFileType',9031,'文档')">文档</li>
              <li @click="handleSelectCategory('resourceFileType',9032,'其他')">其他</li>
            </ul>
          </div>
          <div class="status">
            <span @click.stop="selectType=2">
              {{orderValue}}
              <i class="el-icon-arrow-down"></i>
            </span>
            <ul v-clickOutSide="handleHideSelect" v-show="selectType===2">
              <li @click="handleSelectCategory('orderType', 1,'最新')">最新</li>
              <li @click="handleSelectCategory('orderType', 2,'最热')">最热</li>
              <li @click="handleSelectCategory('orderType', 3,'评分')">评分</li>
            </ul>
          </div>
        </div>
      </div>
      <div class="res-boxs">
        <res-box @click.native="handleTurnTo(item)" :info="item" v-for="item in resourceList" :key="item.resourceId"></res-box>
      </div>
      <resPagination
        v-if="pagination.total / pagination.pageSize > 1"
        :currentPage="pagination.currentPage"
        :total="pagination.total"
        :pageSize="pagination.pageSize"
        @onSizeChange="handlePageChange"
      ></resPagination>
    </div>
    <res-footer></res-footer>

    <!-- nav -->
    <div class="nav">
      <ul>
        <li @click.stop="handleSelectNav(0)" :class="{active:resourceType===0}">
          <i class="iconfont icon-solid-other"></i>
          <p>全部</p>
        </li>
        <li @click.stop="handleSelectNav(9060)" :class="{active:resourceType===9060}">
          <i class="iconfont icon-solid-book"></i>
          <p>学科</p>
        </li>
        <li @click.stop="handleSelectNav(9061)" :class="{active:resourceType===9061}">
          <i class="iconfont icon-solid-flag"></i>
          <p>活动</p>
        </li>
      </ul>
      <div
        v-clickOutSide="handleHideSubNav"
        v-if="(resourceType === 9060 || resourceType === 9061) && showSubNav"
        class="sub-nav"
      >
        <div class="close">
          <i @click="showSubNav = false" class="iconfont icon-arrow-header"></i>
        </div>
        <template v-if="resourceType === 9060">
          <div class="sub">
            <el-select
              v-model="stageId"
              placeholder="全部学段"
              popper-class="popper"
              style="width:100%;margin-bottom:20px"
            >
              <el-option value="-1" label="全部学段"></el-option>
              <el-option
                v-for="item in gradeLists"
                :key="item.code"
                :value="item.code"
                :label="item.text"
              ></el-option>
            </el-select>
            <el-select
              v-model="subjectId"
              popper-class="popper"
              style="width:100%;margin-bottom:20px"
            >
              <el-option value="-1" label="全部科目"></el-option>
              <el-option
                v-for="item in subjectList"
                :key="item.id"
                :value="item.id"
                :label="item.name"
              ></el-option>
            </el-select>
            <el-select
              v-model="gradeId"
              popper-class="popper"
              style="width:100%;margin-bottom:20px"
            >
              <el-option value="-1" label="全部年级"></el-option>
              <el-option
                v-for="item in classLists"
                :key="item.id"
                :value="item.id"
                :label="item.text"
              ></el-option>
            </el-select>
            <el-select v-model="versionId" popper-class="popper" style="width:100%">
              <el-option value="-1" label="全部版本"></el-option>
              <el-option
                v-for="item in versionList"
                :key="item.id"
                :value="item.id"
                :label="item.name"
              ></el-option>
            </el-select>
          </div>
          <div class="select-lists">
            <el-scrollbar style="height:100%">
              <el-tree 
              empty-text="请选择科目"
              :data="treeList" 
              :props="defaultProps" 
              @node-click="handleNodeClick"></el-tree>
            </el-scrollbar>
          </div>
        </template>
        <template v-if="resourceType === 9061">
          <ul class="active">
            <li
              @click="handleSelectSubject(item.id)"
              class="text-overflow"
              v-for="item in activeLists"
              :key="item.id"
            >{{item.subjectName}}</li>
          </ul>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
/* 需要获取ID值 */
export default {
  components: {
    resBox: () => import("@/components/res-box"),
    resFooter: () => import("@/components/res-footer"),
    resPagination: () => import("@/components/res-pagination"),
    resSearch: () => import("@/components/res-search")
  },
  data() {
    return {
      id:0,
      selectType: 0, // 0-不限类型,2-最新
      typeValue: "不限类型",
      orderValue: "最新",
      showSubNav: true,
      /* start */
      resourceType: 0, // 导航类型 0.全部 9060.学科 9061.活动
      resourceName: "", // 搜索名称
      resourceFamily: 0, // 0,全部, 9050资源平台, 9051同步课堂
      resourceFileType: 0, // 0不限类型,9030视频,9031文档,9032其它
      orderType: 1, // 1最新,2最热,3评分
      /* 全部学段 */
      gradeLists: [],
      stageId: "-1",
      /* 全部科目 */
      subjectList: [],
      subjectId: "-1",
      /* 全部年级 */
      classLists: [],
      gradeId: "-1",
      /* 全部版本 */
      versionList: [],
      versionId: "-1",

      /* pagination */
      pagination: {
        total: 0,
        pageSize: 20,
        currentPage: 1
      },
      /* resourceList */
      resourceList: [],
      activeLists: [],

      /* treeList */
      catalogId:"-1",
      treeList:[],
      defaultProps:{
        children: 'children',
        label: 'name'
      },
    };
  },
  created() {
    this.id = this.$route.query.id || 0;
    this.getAllGrade();
    this.getAllClass();
    this.getAllVersion();
    this.getInfo();
    this.getActives();
    this.getSubjectTree();
  },
  watch: {
    /* 分类all-同步课堂 */
    resourceFamily(newValue) {
      this.pagination.currentPage = 1;
      this.resourceFamily = newValue;
      this.getInfo();
    },
    /* 不限类型 */
    resourceFileType(newValue) {
      this.pagination.currentPage = 1;
      this.resourceFileType = newValue;
      this.getInfo();
    },
    /* 最新 */
    orderType(newValue) {
      this.pagination.currentPage = 1;
      this.orderType = newValue;
      this.getInfo();
    },
     /* nav-type  */
    resourceType() {
      this.stageId = "-1";
      this.subjectId = "-1";
      this.gradeId = "-1";
      this.versionId = "-1";
    },
    listenChangeAll() {
      this.getSubjectTree();
      this.getInfo()
    }
  },
  computed: {
    listenChangeAll() {
      const { subjectId, gradeId, versionId, stageId } = this;
      return { subjectId, gradeId, versionId, stageId };
    }
  },
  methods: {
    getInfo() {
      const params = {
        resourceName: this.resourceName,
        resourceFamily: this.resourceFamily,
        resourceFileType: this.resourceFileType,
        orderType: this.orderType,
        pageSize: this.pagination.pageSize,
        currPage: this.pagination.currentPage,
        resourceType: this.resourceType,
        stageId: this.stageId,
        subjectId: this.subjectId, // 科目 活动
        versionId: this.versionId,
        gradeId: this.gradeId,
        catalogId:this.catalogId
      };
      /* 获取id值 */
      this.$api.selectResourceData(this.id, params).then(res => {
        this.pagination.total = Number(res.totalDatas);
        this.resourceList = res.data;
      });
    },
    handleSearch() {
      if (!this.resourceName) return false;
      this.getInfo();
    },
    handlePageChange(currentPage) {
      this.pagination.currentPage = currentPage;
      this.getInfo();
    },
    /* 获取科目树 */
    getSubjectTree() {
      let params = {
        periodId: this.stageId,
        subjectId: this.subjectId,
        textbookVersionId: this.versionId,
        gradeId:this.gradeId
      };
      this.$api.getSubjectTree(params).then(res => {
        // console.log(res.data);
        this.treeList = res.data;
        // console.log("这是一个树目录 todo");
      });
    },
    /* 全部学段 */
    getAllGrade() {
      const params = { type: "stage" };
      this.$api.dictGetByType(params).then(res => {
        this.gradeLists = res.data;
      });
    },

    /* 全部科目 接口*/
    getAllSubject(){
      // 不传 获取 全部科目 传 学段periodId 获取对应的
      this.$api.getSubjectByGrade().then(res => {
        this.subjectList = res.data;
      });
    },
    /* 全部年级 */
    getAllClass() {
      const params = { type: "grade" };
      this.$api.dictGetByType(params).then(res => {
        this.classLists = res.data;
      });
    },
    /* 全部版本 */
    getAllVersion() {
      this.$api.getKeyAndValue().then(res => {
        this.versionList = res.data;
      });
    },
    /* 活动 */
    getActives() {
      const params = {
        pageSize: 2000,
        pageIndex: 1,
        subjectType: 1,
        status: 0
      };
      this.$api.schoolCalendar(params).then(res => {
        this.activeLists = res.data;
      });
    },
    handleHideSelect() {
      this.selectType = 0;
    },
    handleSelectCategory(key, type, value) {
      this[key] = type;
      this.handleHideSelect();
      if (key === "resourceFileType") {
        this.typeValue = value;
      } else {
        this.orderValue = value;
      }
    },
    handleSelectSubject(id) {
      this.subjectId = id;
      this.showSubNav = false;
      this.getInfo();
    },
    /* 左边导航 */
    handleSelectNav(type) {
      this.resourceType = type;
      this.showSubNav = true;
    },
    handleHideSubNav() {
      this.showSubNav = false;
    },
    handleTurnTo(item){
      this.$router.push(`/resource/Layout/resource-list/${item.resourceId}`)
    },
    handleNodeClick(data){
      if(!data.hasNext) {
        this.catalogId = data.id;
        this.getInfo();
        this.handleHideSubNav();
      }
    },
  }
};
</script>

<style lang="scss" scoped="scoped">
.resource {
  min-width: 1200px;
  background: #f5f6f8;
  overflow: hidden;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .main-content {
    width: 81.6%;
    margin: 0 auto;
  }
  .search-type {
    padding: 0 0.8%;
    display: flex;
    justify-content: space-between;
    .left span {
      display: inline-block;
      background: #dcdee3;
      border-radius: 15px;
      padding: 5px 10px;
      margin-right: 15px;
      cursor: pointer;
      &.active {
        color: #ffffff;
        background: #8f9399;
      }
    }

    .right {
      & > div {
        display: inline-block;
        position: relative;
        margin-left: 30px;
        span {
          cursor: pointer;
          color: #606166;
        }
        ul {
          position: absolute;
          z-index: 998;
          left: 0;
          top: 2rem;
          background: #fff;
          box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.2);
          li {
            line-height: 42px;
            padding-left: 18px;
            width: 120px;
            cursor: pointer;
            color: #606266;
            &:hover {
              background: #f3f3f3;
            }
          }
        }
      }
    }
  }
  .res-boxs {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;
    & > div {
      width: 20%;
    }
    @media screen and (max-width: 1520px) {
      & > div {
        width: 25%;
      }
    }
  }

  .nav {
    position: fixed;
    left: 0;
    top: 60px;
    bottom: 0;
    width: 80px;
    background: #181a1e;
    & > ul li {
      height: 80px;
      text-align: center;
      padding: 15px 0;
      cursor: pointer;
      p {
        color: #a9b6cb;
      }
      &.active {
        background: #22272d;
        i,
        p {
          color: #2695ff;
        }
      }
      i {
        font-size: 30px;
      }
    }
    .sub-nav {
      position: absolute;
      left: 80px;
      top: 0;
      bottom: 0;
      width: 260px;
      background: #22272d;
      .close {
        text-align: right;
        padding: 10px;
        i {
          color: #8495ad;
          transform: rotate(180deg);
          display: inline-block;
          cursor: pointer;
        }
      }
      .sub {
        padding: 20px;
      }
      .active {
        padding: 20px;
        li {
          padding: 0 20px;
          height: 52px;
          line-height: 52px;
          text-align: center;
          cursor: pointer;
          color: #a9b6cb;
          &:hover {
            background: rgba(169, 182, 203, 0.1);
            border-radius: 6px;
            color: #2695ff;
          }
        }
      }
      .select-lists {
        height: 500px;
        padding: 20px;
      }
    }
  }
}
</style>
<style>
.resource .el-input__inner {
  background: #22272d;
  border: 1px solid rgba(172, 172, 172, 0.5);
}
.resource .select-lists .el-tree {
  background: #22272d;
}
/* .resource .select-lists .el-tree .el-tree-node__content {
  border-radius: 6px;
  background: #373e46;
} */
.resource .select-lists .el-tree .is-current.is-focusable.el-tree-node .el-tree-node__content{
  /* border-radius: 6px; */
  background: #373e46;
}
.resource .select-lists .el-tree  .el-tree-node__content:hover {
  /* border-radius: 6px; */
  background: #373e46;
}
</style>